import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import App from './App'
import ProfessionalForm from './components/Professionals/ProfessionalForm'
import ProfessionalManager from './components/Professionals/ProfessionalManager'
import ProfessionalEditForm from './components/Professionals/ProfessionalEditForm'

import React from 'react'

export default function routes() {
    return (
        <Router basename={process.env.PUBLIC_URL}>
            <Switch>
                <Route exact path="/">
                    <App />
                </Route>
                <Route exact path="/professional/add">
                    <App component={<ProfessionalForm />} />
                </Route>
                <Route exact path="/professional">
                    <App component={<ProfessionalManager />} />
                </Route>
                <Route exact path="/professional/edit">
                    <App component={<ProfessionalEditForm />} />
                </Route>
            </Switch>
        </Router>
    )
}
